*,
*::before,
*::after {
  box-sizing: inherit;
}

html {
  box-sizing: border-box;
  font-size: 16px;
}

body {
  display: flex;
  flex-direction: column;
  width: 100%;
  min-height: 100vh;
  line-height: 1.2;
  background-color: #ffffff;
  color: #ffffff;
}

a {
  text-decoration: none;
}

img {
  height: auto;
  max-width: 100%;
}

h1,
h2,
h3,
h4,
p,
figure,
fieldset,
ul {
  margin: 0;
}

ul {
  padding-left: 0;
}

li {
  list-style: none;
}

input,
button,
select,
a,
textarea,
label,
div {
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

fieldset {
  border: none;
  padding: 0;
}

button:focus,
input:focus,
a:focus {
  outline: none;
}

button {
  border: none;
  padding: 0;
}

.container {
  margin-right: auto;
  margin-left: auto;
  padding-right: 15px;
  padding-left: 15px;
  max-width: 1350px;
}

.body-exit {
  justify-content: center;
  align-items: center;
  background: #181820;
}

.main-exit {
  width: 100%;
}

.exit {
  width: 100%;
}

.form-exit {
  padding-top: 48px;
  padding-bottom: 48px;
  padding-left: 40px;
  padding-right: 40px;
  background: #21212b;
  border-radius: 8px;
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  max-width: 440px;
  margin-left: auto;
  margin-right: auto;
}

.form-exit__field {
  width: 100%;
}

.form-exit__legend {
  font-weight: 500;
  font-size: 18px;
  line-height: 23px;
  color: #ffffff;
  margin-bottom: 48px;
  margin-left: auto;
  margin-right: auto;
  text-align: center;
}

.form-exit__img-wrap {
  width: 42px;
  height: 42px;
  border-radius: 50%;
  background-color: #272732;
  margin-left: auto;
  margin-right: auto;
  position: relative;
}

.form-exit__img-wrap::after {
  position: absolute;
  content: "";
  width: 18px;
  height: 12px;
  background-image: url(/img/svg/login-arr.svg);
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}

.form-exit__box + div {
  margin-top: 16px;
}

.form-exit__label {
  display: block;
}

.form-exit__label span:not(.withdrawal__label) {
  position: absolute;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px;
}

.form-exit__input {
  width: 100%;
  background: #272732;
  border: 1px solid #292936;
  border-radius: 2px;
  padding: 21px 16px;
  font-size: 14px;
  line-height: 18px;
  color: #90969e;
}

.form-exit__input::placeholder {
  font-size: 14px;
  line-height: 18px;
  color: #90969e;
}

.form-exit__text {
  font-size: 14px;
  line-height: 18px;
  margin-top: 24px;
  text-align: center;
  color: #90969e;
}

.form-exit__btn {
  width: 100%;
  color: #ffffff;
  font-size: 14px;
  line-height: 18px;
  padding-top: 21px;
  padding-bottom: 21px;
  background: linear-gradient(360deg, #ff6240 0%, #d50000 100%);
  border-radius: 4px;
  margin-top: 32px;
  cursor: pointer;
  transition: 0.3s;
  text-align: center;
}

.form-exit__btn:hover,
.form-exit__btn:focus {
  opacity: 0.6;
}

.form-exit__forget {
  font-weight: 500;
  font-size: 14px;
  line-height: 18px;
  color: #90969e;
  margin-top: 24px;
  transition: 0.3s;
}

.form-exit__forget:hover,
.form-exit__forget:focus {
  opacity: 0.6;
}

.not-valid {
  border-color: red;
}

.not-valid::placeholder {
  color: red;
}

.header-exit {
  margin-bottom: 50px;
}

@media screen and (max-width: 768px) {
  .body-exit {
    justify-content: flex-start;
    padding-top: 60px;
    padding-bottom: 60px;
  }
}

@media screen and (max-width: 600px) {
  .form-exit {
    max-width: none;
    padding: 40px 20px;
  }
}